import { max } from '../utils/math';
import { _ } from '../utils/constants';
export function calculatePlayers(model) {
    model.duration = max.apply(_, model.players.filter(a => isFinite(a.to)).map(a => a.to));
    model.time = isFinite(model.time)
        ? model.time
        : model.rate < 0
            ? model.duration
            : 0;
}
